import router from "../../../router";
import { authenticateUser } from "../../../services/auth";

export default {
    name: "login",
    data() {
        return {
            companyCode: '',
            email: '',
            password: '',
            errorMsg: '',
        };
    },
    methods: {

        onLoginSubmit(event) {
            this.errorMsg = '';
            // console.log(this.companyCode)
            var params = {
                companyCode: this.companyCode,
                email: this.email,
                password: this.password
            };

            event.preventDefault();
            authenticateUser(params).then((result) => {
                if (result.data.success) {
                    this.$store.dispatch("setLoginStatus", true);
                    // console.log(result.data.userInfo)
                    this.$store.dispatch("setUserInfo", {
                        "userId": result.data.userInfo.userId,
                        "first_name": result.data.userInfo.first_name,
                        "last_name": result.data.userInfo.last_name,
                        "companyCode": this.companyCode,
                        "projectCode": result.data.userInfo.projectCode,
                        "role": result.data.userInfo.role,
                        "projectList": result.data.userInfo.projectList,
                        "hasProject": result.data.userInfo.hasProject
                    });
                    if (result.data.userInfo.hasProject) {
                        router.push({ name: 'SessionList' });
                    } else {
                        this.errorMsg = result.data.message;
                    }

                } else {
                    //Show invalid error message
                    this.errorMsg = result.data.message;
                }
            }).catch((error) => {
                //Internal error
                this.errorMsg = "Internal Error.";
            });

        },
    }
};


